








































































import { Vue, Component } from 'vue-property-decorator'

@Component({
  name: 'Notice'
})
export default class Notice extends Vue {
  private visible: boolean = false
  private loading: boolean = false
  private fetchNotice() {
    if (!this.visible) {
      this.loading = true
      setTimeout(() => {
        this.loading = false
      }, 2000)
    } else {
      this.loading = false
    }
    this.visible = !this.visible
  }
}
